.choice-theme {
    position: absolute;
    right:160px;
    bottom:-45px;
    background: white;
    padding-top: 0;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    z-index: 10;
    @include main-shadow;
    &__container {
        display: flex;
    }
    &__title {
        font-weight: bold;
        text-transform: uppercase;
        font-weight: 10px;
        margin-bottom: 10px;
        text-align: center;
    }
    &__radio {
        display: flex;
    }
    &__item {
       position: relative;
       margin:5px;
       margin-left: 10px;
       margin-right: 10px;
    }
    &__radio {
        &:checked {
            + {
                .choice-theme__label::before {
                    transform: scale(1.5);
                }
                .choice-theme__label--blue::before {
                    border:2px solid #8C50FF;
                }
                .choice-theme__label--red::before {
                    border:2px solid #FF5099;
                }
                .choice-theme__label--green::before {
                    border:2px solid #50FFCA;
                }
                .choice-theme__label--yellow::before {
                    border:2px solid #F1EA42;
                }
            }
        }
    }
    
    &__label {
        width: 25px;
        height: 25px;
        border-radius: 100%;
        display: block;
        position: relative;
        transition: all 0.4s ease;
        &:hover {
            cursor: pointer;
        }
        &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 25px;
            height: 25px;
            transform: scale(0);
            transition: all 0.4s ease;
            border-radius: 100%;
        }
        &--blue {
            background: #8C50FF;
        }
        &--red {
            background: #FF5099;
        }
        &--green {
            background: #50FFCA;
        }
        &--yellow {
            background: #F1EA42;
        }
    }
   
}
