.loader{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 0;
    top:0;
    height: 100%;
    width: 100%;
    background: var(--page-background);
    z-index: 1000;
    &__balls {
        display: flex;
    }
    &__ball {
        margin: 5px;
        width: 300px;
        height: 300px;
        border-radius: 50%;
        width:20px;
        height: 20px;
      
        &--red {
            background-color: #FF5099;
            animation: ball 1.7s infinite ease 0s;
        }
        &--blue {
            background-color: #8C50FF;
            animation: ball 1.7s infinite ease .1s;
        }
        &--green {
            background-color: #50FFCA;
            animation: ball 1.7s infinite ease .2s;
        }
        &--yellow {
            background-color: #F1EA42;
            animation: ball 1.7s infinite ease .3s;
        }
    }
    &__text {
        color:black;
        text-decoration: none;
        font-family:'Roboto',sans-serif;
        font-weight:bold;
        animation: loading 1s infinite linear alternate;
    }
}

@keyframes ball{
    0%{
        margin-top: 0px;
    }
    10%{
        margin-top: -50px;
    }
    20%{
        margin-top: 0px;
    }
    100%{
        margin-top: 0px;
    }
}
@keyframes loading {
  0% {
    opacity:0;
  }100% {
    opacity:1;
  }
}

