html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: $font-family, sans-serif;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  background: var(--page-background);
  overflow-x: hidden;
  z-index: -999;
  position: relative;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}
.link {
  color:black;
  position: relative;
}
.link:active {
  color: var(--main-color);
}
.link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  height: 2px;
  border-radius: 50px;
  width: 100%;
  background-color: var(--main-color);
  opacity: 0.5;
}
.link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  height: 2px;
  border-radius: 50px;
  width: 100%;
  transform: scaleX(0);
  transition: transform 0.2s ease;
  background-color: var(--main-color);
  transform-origin: right bottom;
}
.link:hover::after {
  transform: scaleX(1);
  transform-origin: left bottom;
  transition: transform 0.2s ease;
}
.site-container {
  // margin: 0 auto;
  // padding: 0 var(--container-offset);
  // max-width: var(--container-width);
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 $container-offset;
  max-width: $container-width;
}
.container-fluid {
  padding: 0 $container-offset;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

ul
{
list-style: none;
padding: 0;
margin: 0;
}