.front-screen{
    display: flex;
    margin-bottom: 120px;
    min-height: calc(100vh - 160px);
    position: relative;
    &__abstract {
        position: absolute;
        right: 0;
        top: 0;
        z-index: -1;
        height: 100%;
        overflow: hidden;
        svg ellipse,
        svg path{
            fill: var(--main-color);
        }
    }
    &__landing {
        background: #fff;
        box-shadow: -10px 10px 0px var(--main-color);
        padding-left: 40px;
        padding-right: 20px;
        padding-top: 100px;
        flex-basis: 70%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: relative;
        z-index: 10;
    }
    &__title {
        text-transform: uppercase;
        font-size: rem(64);
        margin-bottom: 0px;
        margin-top: 0;
    }
    &__word {
        color: var(--main-color);
    }
    &__subtitle {
        text-transform: uppercase;
        font-weight: 400;
        font-size: rem(32);
        margin-top: 0;
    }
    &__text {
        font-weight: 300;
        font-size: rem(22);
        max-width: 600px;
    }
    &__navigation{
        background:var(--primary-title-color);
        flex-basis: 30%;
    }
    &__button {
        @include black-button;
        margin-top: auto;
        margin-bottom: 20px;
        margin-left: -75px;
    }

}
.navigation--darken {
    margin-top: 100px;
    .navigation__link {
        color: white;
        z-index: 10;
    }
  
}