.tabs {
    margin-bottom: 100px;
    &__content {
        @include main-shadow;
        background:white;
        display: none;
        &--active {
            display: block;
        }
    }
    &__list {
        display: flex;
        flex-wrap: wrap;
    }
    &__btn {
        padding-left: 60px;
        padding-right: 60px;
        padding-top: 20px;
        padding-bottom: 20px;
        text-align: center;
        background:white;
        border: none;
        color: var(--main-color);
        text-transform: uppercase;
        font-size: 16px;
        font-weight: 500;
        position: relative;
        margin-right: 20px;
        cursor: pointer;
        &--active {
            &::before {
                opacity: 1 !important;
            }
        }
        &::before {
            content: "";
            position: absolute;
            left:0;
            bottom: 0;
            height: 4px;
            width: 100%;
            background: var(--main-color);
            opacity: 0.5;
            transition: all 0.4s ease;

        }
        &:hover {
            &::before {
                opacity: 1;
                transition: all 0.4s ease;
            }
        }
    }
}
.content {
    padding-top: 40px;
    padding-left: 40px;
    padding-bottom: 40px;
    padding-right: 40px;
    &__title {
        margin:0;
        text-transform: uppercase;
        font-weight: 700;
        font-size: rem(32);
        color: var(--primary-title-color);
        margin-bottom: 20px;
    }
    &__description {
        font-size: rem(22);
        font-weight: 500;
        margin-bottom: 40px;
    }
    &__btn {
        @include black-button;

    }
}