.blog {
    margin-bottom: 100px;
    &__title-section {
        font-size: rem(72);
        font-weight: 700;
        color: var(--primary-title-color);
        margin-bottom: 50px;
    }
    &__link-section {
        font-weight: 500;
        font-size: rem(28);
        padding-bottom: 10px;
    }
    &__link-container {
        text-align: center;
    }
    &__cards {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    &__card {
        margin-bottom: 30px;
        flex-basis: calc(100% / 2 - 15px);
        flex-direction: column;
        display: flex;
        background: white;
        border-bottom: 2px solid var(--main-color);
        transition: all 0.2s ease;
        @include main-shadow;
        &:hover {
            transform: translateY(-10px);
            transition: all 0.2s ease;
        }
        .blog__content {
            padding-left: 50px;
            padding-top: 25px;
            padding-bottom: 50px;
            padding-right: 25px;
            flex-basis: 0;
        }
        .blog__image-container {
            order: -1;
            max-height: 400px;
            flex-basis: 60%;
        }
        &:nth-child(3n) {
            flex-basis: 100%;
            display: flex;
            flex-direction: row;
            min-height: 350px;
           .blog__content {
               flex-basis: 70%;
               padding-left: 50px;
               padding-top: 25px;
               padding-bottom: 25px;
               padding-right: 25px;
           }
           .blog__image-container {
                max-height: 400px;
                order: 1;
                flex-basis: 30%;
            }
        }



    }
    &__categories {
        flex-grow: 1;
    }
    &__title {
        margin-top: 30px;
        font-size: rem(30);
        text-transform: uppercase;
    }
    &__subtitle {
        font-size: rem(24);
        font-weight: 200;
    }
    &__link {
        font-size: rem(18);
        font-weight: 500;
    }
    &__category {
            &-link {
                font-size: rem(14);
                font-weight: 500;
                // text-transform: uppercase;
                background: rgba(var(--main-color-rgb), 0.2);
                padding: 10px;
                display: inline-block;
                margin-right: 5px;
                margin-bottom: 5px;
                cursor: pointer;
                color: var(--main-color);
                transition: all 0.2s ease;
                &:hover {
                    background: var(--main-color);
                    color: white;
                    transition: all 0.2s ease;
                }
            }
    }
    &__date {
        color: #B9B9B9;
        font-size: rem(16);
        font-weight: 200;
    }
    &__content {
        background: white;
    }
    &__info {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
    }
    &__image {
        height: 100%;
        width: 100%;

        &-container {
            position: relative;
            max-height: 400px;
            flex-basis: 30%;
            overflow: hidden;
            &::before {
                content: "";
                @include blackout;
            }
        }
    }
}