@mixin black-button {
    background: #0D0D0D;
    color:white;
    font-weight: bold;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 60px;
    padding-right: 60px;
    text-transform: uppercase;
    font-size: 14px;
    transition: all 0.2s ease;
    display: inline-block;
    position: relative;
    z-index: 1;
    border: none;
    &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        height: 100%;
        width: 100%;
        transform: scaleX(0);
        transition: transform 0.2s ease;
        background-color: var(--main-color);
        transform-origin: right bottom;
        z-index: -1;
      }
    &:hover {
      cursor: pointer;
        &::after {
            transform: scaleX(1);
            transform-origin: left bottom;
            transition: transform 0.2s ease;
          }
    }


}