/* stylelint-disable */

@mixin burger {
  $burger-width: 25px;
  $burger-height: 18px;
  $burger-line-height: 2px;
  $burger-active-bg:#f92c8c;
  position: relative;
  border-radius: none;
  padding: 0;
  width: $burger-width;
  height: $burger-height;
  // color: var(--main-color);
  background-color: transparent;
  cursor: pointer;
  &:hover {
    &::before {
        background-color: $burger-active-bg;
    }
    &::after {
        background-color: $burger-active-bg;
    }
    .burger__line {
        background-color: $burger-active-bg;
    }
  }
  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: $burger-line-height;
    background-color: var(--main-color);
    transition: all .4s ease;
  
  }

  &::before {
    top: 0;
  }

  &::after {
    top: calc(100% - $burger-line-height);
  }

  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: $burger-line-height;
    background-color: var(--main-color);
    transform: translateY(-50%) scale(1);
    transition: all .4s ease;

  }

  &--active {
    &::before {
      top: 50%;
      transform: rotate(45deg);
      transition: all .4s ease;
      background-color: $burger-active-bg;
      
    }

    &::after {
      top: 50%;
      transform: rotate(-45deg);
      transition: all .4s ease;
      background-color: $burger-active-bg;
    }

    .burger__line {
      transform: translateY(-50%) scale(0);
      transition: all .4s ease;
      background-color: $burger-active-bg;
    }
  }
}

@mixin burger-menu {
    position:fixed;
    right:0;
    top:0;
    background: white;
    height:100vh;
    width:300px;
    z-index:990;
    transform:translateX(100%);
    transition: all .4s ease;
    padding-top:100px;
    text-transform:uppercase;
    @include main-shadow;
    &--active {
      transform:translateX(0);
    }
}