.faq {
    background: white;
    padding: 50px;
    margin-bottom: 100px;
    @include main-shadow;

    &__title-section {
        font-size: rem(72);
        font-weight: 700;
        color: var(--primary-title-color);
        margin: 0;
        margin-bottom: 50px;
    }
}
.accordion {
    margin: 5px;
    flex-basis: calc(100% / 2 - 10px);
    &__list {
        display: flex;
        flex-wrap: wrap;
    }
    &__control {
        width: 100%;
        padding-top: 20px;
        padding-bottom: 20px;
        padding-left: 80px;
        padding-right: 20px;
        background-color: transparent;
        border: 1px solid #B1B4C4;
        font-size: rem(22);
        font-weight: 500;
        transition: all 0.2s ease;
        text-align: left;
        cursor: pointer;
        position: relative;
        &:hover {
            border: 1px solid var(--main-color);
            transition: all 0.2s ease;

        }
        &::before {
            content: "";
            position: absolute;
            background: var(--main-color);
            height: 2px;
            width: 30px;
            left: 25px;
            top: 50%;
        }
        &::after {
            content: "";
            position: absolute;
            background: var(--main-color);
            height: 2px;
            width: 30px;
            left: 25px;
            top: 50%;
            transform: rotate(90deg);
            transition: all 0.2s ease;
        }
    }
    &__content {
        background-color: transparent;
        border: 1px solid #B1B4C4;
        border-top: none;
        font-size: rem(18);
        font-weight: 300;
        max-height: 0;
        opacity: 0;
        overflow: hidden;
        padding:0 20px;
        will-change: max-height;
        transition: all 0.2s ease;
        box-sizing: content-box;
    }
}
.open {
    .accordion__content {
        opacity: 1;
        padding: 20px;
    }
    .accordion__control {
        &::after {
            transform: rotate(0deg);
            transition: all 0.2s ease;
        }
    }
}