$font-family: "Ubuntu";
// --content-width: 1170px;
$container-offset: 70px;
// --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
$container-width:2048px;
// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  font-size: 16px;
  --page-background:#FDF8FF;
  --primary-title-color:#333030;
  --primary-text-color:#000;
}

body[data-theme="blue"] {
  --main-color:#8C50FF;
  --main-color-rgb:140, 80, 255;
}
body[data-theme="red"] {
  --main-color:#FF5099;
  --main-color-rgb:255, 80, 153;;
}
body[data-theme="green"] {
  --main-color:#50FFCA;
  --main-color-rgb:80, 255, 202;
}
body[data-theme="yellow"] {
  --main-color:#F1EA42;
  --main-color-rgb:241, 234, 66;
}
::selection {
  background-color: rgba(#FFCEA9, 0.5); 
}
:focus {
  outline-color: var(--main-color);
  outline-offset: 5px;
  
}