.portfolio-slider {
    position: relative;
    display: flex;
    height: 800px;
    margin-bottom: 100px;
    overflow: hidden;
    &__wrapper {
      // width: 90%;
    }
    &__slide {
      display: flex;
      width: 100% ;
    }
    &__left-side {
        background:white;
        padding-left: 30px;
        padding-top: 30px;
        padding-right: 30px;
        padding-bottom: 30px;
        flex-basis: 40%;
        position: relative;
    }
    &__category {
        font-size: rem(14);
        font-weight: 500;
        text-transform: uppercase;
        background: rgba(var(--main-color-rgb), 0.2);
        padding: 10px;
        color: var(--main-color);
        display: inline-block;
    }
    &__title {
        margin-top: 60px;
        margin-left: 20px;
        padding-left: 5px;
        font-weight: 700;
        font-size: rem(36);
        border-left: 8px solid var(--main-color);
    }
    &__description {
        margin-top: 40px;
        font-weight: 200;
        margin-left: 30px;
        font-size: rem(24);
    }
    &__btn {
        @include black-button;
        position: absolute;
        right: -100px;
        bottom: 20px;
    }
    &__image-container {
        flex-basis: 60%;
        position: relative;
        &::before {
            content: "";
            @include blackout;
        }
    }
    &__image {
        height: 100%;
        width: 100%;
    }
    &__sidebar {
        width: 10%;
        background: white;
        padding: 10px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-top: 20px;
        padding-bottom: 20px;
        align-items: center;
        position: relative;
        z-index: 999;
        min-width: 150px;
        // position: absolute;
        // right: 0;
        // top:0;
        // bottom: 0;
    }
    &__numbers {
        text-align: center;
        font-weight: 700;
        font-size: rem(24);
        margin-top: 0;
    }
    &__current {
        font-size: rem(48);
        color: var(--main-color);
        display: inline-block;
    }
    &__count {
        font-size: rem(26);
    }
    &__link {
        writing-mode: tb-rl;
        font-size: rem(24);
        font-weight: 700;
        text-transform: uppercase;
        &:hover {
            &::after {
                transform: scaleY(1);
                transform-origin: left top;
                transition: transform 0.2s ease;
            }
        }
        &::before {
            height: 100%;
            width: 2px;
            left: -10px;
        }
        &::after {
            left: -10px;
            transform: scaleY(0);
            height: 100%;
            width: 2px;
            transform-origin: left bottom;
            transition: transform 0.2s ease;
        }
    }
    &__buttons {
        display: flex;
        justify-content: center;
    }
    &__button {
        border: 4px solid var(--main-color);
        border-radius: 100%;
        padding: 25px;
        background: transparent;
        margin: 5px;
        position: relative;
        cursor: pointer;
        transition: all 0.4s ease;
        &:disabled {
          opacity: 0.4;
          pointer-events: none;
        }
        &:hover {
            background: var(--main-color);
            transition: all 0.4s ease;
            &::before,
            &::after {
                background: white;
                transition: all 0.4s ease;
            }
        }
        &::before,
        &::after {
            content:"";
            position: absolute;
            background: var(--main-color);
            height: 4px;
            width: 20px;
            border-radius: 100px;
        }
        &--right {
            &::before {
                transform:translate(-40%,-200%) rotate(45deg);
            }
            &::after {
                transform:translate(-40%, 100%) rotate(-45deg);
            }
        }
        &--left {
            &::before {
                transform:translate(-60%,-200%) rotate(-225deg);
            }
            &::after {
                transform:translate(-60%, 100%) rotate(225deg);
            }
        }
    }
}



.slider-container {
  box-shadow: -5px -5px 0px var(--main-color);
  position: relative;
  display: flex;
  height: 800px;
  background: white;
}

