.navigation {
    font-size:24px;
    font-weight:bold;
    padding-left:50px;
    position:relative;
    padding:30px;
    &::before {
        content:"";
        position:absolute;
        left:30px;
        top:0;
        height:100%;
        width:4px;
        background:var(--main-color);
    }
    &__item {
        margin-bottom: 10px;
        padding-left: 30px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__link {
        color:var(--primary-title-color);
        text-transform: uppercase;
        display: inline-block;
        position: relative;
        &::before {
            content:"";
            position:absolute;
            left:0;
            bottom:-2px;
            height:0;
            width:100%;
            background:var(--main-color);
            transition: all .2s ease;
            z-index:-1;
        }
        &:hover {
            &::before {
                height: 15px;
            }    
        }
    }
    &__round {
        position:absolute;
        left:22px;
        top:4px;
        height:20px;
        width:20px;
        border-radius:100%;
        background:var(--main-color);
        transition: all .7s ease;
    }
}
.burger-menu {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include burger-menu;
}
.page-blackout {
    transition: all .4s ease;
    &--active {
        @include blackout;
        transition: all .4s ease;
    }
}
.choice-theme.choice-theme-menu {
    position: static;
    height: 100px;
    background: none;
    box-shadow: none;

}
.choice-theme__container {
    justify-content: center;
}