.social {

    z-index: 10;
    &__item {
        width: 60px;
        height: 60px;
        background: white;
        border: 1px solid black;
        box-shadow: -5px 5px 0px var(--main-color);
        display: inline-block;
        transition: all 0.2s ease;
        margin-bottom: 20px;
        @include flex-all-center;
        &:hover {
            transform:translate(-5px, 5px);
            box-shadow: 0px 0px 0px;
            background: var(--main-color);
            transition: all 0.2s ease;
            svg path{
                fill: white;
                transition: all 0.2s ease;
            }
        }
    }
    &--front-screen {
        position: absolute;
        display: flex;
        flex-direction: column;
        right: -36px;
        bottom: 0;
    }
    &--portfolio-slider{
        position: absolute;
        display: flex;
        flex-direction: column;
        left: -32px;
        bottom: 20px;
    }
}
